import router from '../router/index'

export const selectHero = ({ commit }, hero) => {
  router.push('/' + hero.Display_Name)
  commit('SELECT_HERO', hero)
}

export const increaseAbility = ({ commit }, abilityName) => {
  commit('INCREMENT_LEVEL', abilityName)
}

export const decreaseAbility = ({ commit }, abilityName) => {
  commit('DECREMENT_LEVEL', abilityName)
}

export const filterHeroesTrait = ({ commit }, filter) => {
  commit('FILTER_HEROES_TRAIT', filter)
}

export const filterHeroesName = ({ commit }, filter) => {
  commit('FILTER_HEROES_NAME', filter)
}



