import Vue from 'vue'
import Vuex from 'vuex'

import * as getters from './getters'
import * as actions from './actions'

import * as h from '../assets/M3HeroSheet.json'
import * as d from '../assets/characters.json'

const p = require('papaparse')
var csvdat = require('../assets/data.csv')

var trimmedcsv = csvdat.replace(/^(?!.*ABILITY.*).+$/gm, '')
var csvda = p.parse(trimmedcsv, {skipEmptyLines: true}).data
var obj = {}
csvda.forEach(function (item) {
  obj[item[0]] = item[1]
})
var keys = Object.keys(h)

Vue.use(Vuex)

const state = {
  currentHero: {},
  abilityLevels: {
    'basic': 1,
    'special': 1,
    'ultimate': 1,
    'passive': 1
  },
  abilityMax: {
    'basic': 7,
    'special': 7,
    'ultimate': 7,
    'passive': 5
  },
  heroes: h,
  details: d.Data,
  csvdata: obj,
  isHeroSelected: false,
  heroKeys: keys,
  filteredHeroes: keys
}

const mutations = {
  SELECT_HERO (state, hero) {
    state.currentHero = hero
    state.isHeroSelected = true
  },

  INCREMENT_LEVEL (state, abilityName) {
    (state.abilityLevels[abilityName] < state.abilityMax[abilityName]) ? state.abilityLevels[abilityName] ++ : state.abilityLevels[abilityName] = state.abilityMax[abilityName]
  },

  DECREMENT_LEVEL (state, abilityName) {
    (state.abilityLevels[abilityName] > 1) ? state.abilityLevels[abilityName] -- : state.abilityLevels[abilityName] = 1
  },

  FILTER_HEROES_TRAIT (state, filter) {
    return Object.keys(state.heroes).filter(
      key => {
        d.Data[key]['traits'].includes(filter)
      }
    )
  },

  FILTER_HEROES_NAME (state, f) {
    return Object.keys(state.heroes).filter(
      key => {
        h[key].Display_Name.includes(f)
      }
    )
  }
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



