<template>
  <div class="hello" v-if="isHeroSelected">
    <div class="heroName">
      <b>Hero: </b>{{currentHero.Display_Name}}
    </div>
    <div class="traits">
      <b>Traits: </b>{{details[currentHero.id].traits.join(', ')}}
    </div>
    <section class="abilities">
      <Ability abilityN="basic"></Ability>
      <Ability abilityN="special"></Ability>
      <Ability v-if="!details[currentHero.id].traits.includes('Minion')" abilityN="ultimate"></Ability>
      <Ability abilityN="passive"></Ability>
    </section>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Ability from './Ability'

export default {
  components: {
    Ability
  },
  computed: {
    ...mapGetters([
      'abilityLevels',
      'abilityMax',
      'csvdata',
      'details',
      'currentHero',
      'isHeroSelected'
    ])
  },
  methods: {
    ...mapActions([
      'increaseAbility',
      'decreaseAbility'
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.hello {
  background: darkgray;
  height: 100%;
  padding: 20px;
}
.abilities {
  padding: 10px;
}
</style>



