<template>
  <div class="abilityContainer">
    <div class="columns">
      <div class="column is-four-fifths">
        <p class="is-size-5">{{getAbilityName()}}</p>
        <div v-html="formatAbilityString(abilityLevels[abilityN])"></div>
      </div>
      <div class="column">
        <div class="level">
          <i class="fa fa-minus level-item" aria-hidden="true" v-on:click="decreaseAbility(abilityN)"></i>
          <label class="level-item">{{abilityLevels[abilityN]}}</label>
          <i class="fa fa-plus level-item" aria-hidden="true" v-on:click="increaseAbility(abilityN)"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Ability',
  props: [
    'abilityN'
  ],
  computed: {
    ...mapGetters([
      'abilityLevels',
      'abilityMax',
      'csvdata',
      'details',
      'currentHero',
      'isHeroSelected'
    ])
  },
  methods: {
    ...mapActions([
      'increaseAbility',
      'decreaseAbility'
    ]),
    getAbilityName () {
      var abil = 'ID_ABILITY_' + this.currentHero.id.toUpperCase() + '_' + this.abilityN.toUpperCase() + '_NAME'
      return this.csvdata[abil]
    },
    tempmethod (dobj, pobj, level) {
      var step
      (isNaN(parseInt(pobj))) ? step = pobj : step = parseInt(pobj)
      if (dobj.hasOwnProperty(step)) {
        return dobj[step]
      } else {
        if (['p', 't', 'f'].includes(step)) {
          console.log('DEBUG: ' + step + ' is p, t, or f.  Need to apply level on %o', dobj)
          dobj = dobj[level - 1]
          if (dobj.hasOwnProperty(step)) {
            return dobj[step]
          }
        }
        console.log('%o does not have ' + step + ' property.  Might be an issue.  Returning', dobj)
        return dobj
      }
    },
    matchAbilityStrings (string) {

    },
    getCharDataForAbility (string, level) {
      var stri = string.replace(/\.action\./g, '.actions.')
      var objectPropArray = stri.split('.')

      var dobj = this.details[this.currentHero.id]
      var func = this.tempmethod
      objectPropArray.shift()
      objectPropArray.forEach(function (pobj) {
        dobj = func(dobj, pobj, level)
      })
      var final
      if (Array.isArray(dobj)) {
        final = dobj[level - 1]
      } else {
        final = dobj
      }
      return final
    },
    formatAbilityString (level) {
      var baseString = this.getAbilityData(level)
      var replaced = baseString.replace(/<color=(.*?)>(.*?)<\/color>/g, "<span class='ability-$1'>$2</span>")
      var reg = /\{(.+?)\}/
      var newstring = ''
      var match = reg.exec(replaced)
      while (match != null) {
        newstring = replaced.replace(/\{(.+?)\}/, this.getCharDataForAbility(match[1], level))
        match = reg.exec(newstring)
        replaced = newstring
      }
      return replaced
    },
    getHighestDesc (level) {
      var retstring
      var csvString = 'ID_ABILITY_' + this.currentHero.id.toUpperCase() + '_' + this.abilityN.toUpperCase() + '_' + 1 + '_DESC'
      if (csvString in this.csvdata) {
        retstring = this.csvdata[csvString]
      }
      for (var i = 1; i < level; i++) {
        csvString = 'ID_ABILITY_' + this.currentHero.id.toUpperCase() + '_' + this.abilityN.toUpperCase() + '_' + i + '_DESC'
        if (csvString in this.csvdata) {
          retstring = this.csvdata[csvString]
        }
      }
      return retstring
    },
    getUpgrade (level) {
      var retstring
      for (var i = 1; i < level; i++) {
        var csvString = 'ID_ABILITY_' + this.currentHero.id.toUpperCase() + '_' + this.abilityN.toUpperCase() + '_' + i + '_UPGRADE'
        if (csvString in this.csvdata) {
          retstring = this.csvdata[csvString]
        }
      }
      return retstring
    },
    getAbilityData (level) {
      return this.getHighestDesc(level)
    }
  }
}
</script>

<style>
.ability-yellow {
  color: yellow;
}

.ability-lime {
  color: lime;
}

.abilityContainer {
  padding: 10px;
  text-align: left;
}
</style>



