<template>
  <div id="notes">
    <div class="filterbar">
      <input v-model="traitFilter" placeholder="Search..." @change="traitFilterer(traitFilter)" class="search">
      <button class="button is-small" v-on:click='toggle()' v-bind:class="{ 'is-primary': playable }" title="Filter Playable Characters">
        <span class="icon is-small">
          <i class="fa fa-thumbs-o-up"></i>
        </span>
      </button>
    </div>
    <div class="buttons is-centered">
      <button v-for="hero in filteredList(traitFilter)" :key="hero" v-on:click="selectHero(heroes[hero])" class="button">{{heroes[hero].Display_Name}}</button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  data: function () {
    return {
      heroFilter: '',
      traitFilter: '',
      list: [],
      playable: true,
      playableList: [],
      all: []
    }
  },
  computed: {
    ...mapGetters([
      'heroes',
      'details',
      'heroKeys',
      'currentHero',
      'filteredHeroes'
    ])
  },
  methods: {
    ...mapActions([
      'selectHero',
      'filterHeroesName',
      'filterHeroesTrait'
    ]),
    filteredList: function (f) {
      var p = (this.playable) ? this.playableList : this.all
      return p.filter(
        key => (this.details[key]['traits'].includes(f) || this.heroes[key].Display_Name.toLowerCase().includes(f.toLowerCase()))
      )
    },
    traitFilterer: function (f) {
      var p = (this.playable) ? this.playableList : this.all
      this.$data.list = p.filter(
        key => this.details[key]['traits'].includes(f)
      )
    },
    nameFilterer: function (f) {
      var p = (this.playable) ? this.playableList : this.all
      this.$data.list = p.filter(
        key => this.heroes[key].Display_Name.toLowerCase().includes(f.toLowerCase())
      )
    },
    toggle: function () {
      this.$data.playable = !this.$data.playable
    }
  },
  mounted () {
    this.all = this.$store.getters.filteredHeroes
    this.playableList = this.$store.getters.filteredHeroes.filter(
      key => this.heroes[key].blacklist_mode === 0
    )
    this.list = this.playableList
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}

.notes {
  padding: 20px;
}

.filterbar {
  margin-top: .5rem;
  padding-bottom: .5rem;
}

.search {
  margin-top: .1rem;
}
</style>



